/*********** CSS RESET **********/
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  margin: 0;
  -webkit-text-size-adjust: none;
}

ol, ul {
  list-style: none;
}

a img {
  border: none;
}

button::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=file] > button::-moz-focus-inner,
input[type=file] > input[type=button]::-moz-focus-inner {
  margin: 0;
  border: 0;
  padding: 0;
}

button,
input[type=button],
input[type=submit],
input[type=reset],
input[type=text],
input[type=email],
input[type=tel],
input[type=password],
textarea {
  border-radius: 0;
}

button,
input[type=button],
input[type=submit],
input[type=reset] {
  -webkit-appearance: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 1000px #fff;
}

script {
  display: none !important;
}

/*********** GLOBAL STYLES **********/
body {
  max-width: 100%;
  overflow-x: hidden;
  background-color: #fff;
  font: normal 14px Arial, Helvetica, sans-serif;
  color: #777;
}

h1, h2, h3, h4, h5, h6 {
  color: #444;
}

a {
  color: inherit;
  cursor: pointer;
}

a img {
  opacity: 0.99; /*firefox scale bug fix*/
}

table {
  width: 100%;
  border-collapse: collapse;
}

input[type=text],
input[type=email],
input[type=tel],
input[type=password],
textarea, select {
  height: 36px; /*safari padding fix*/
  border: 1px solid #ddd;
  padding: 8px;
  vertical-align: middle;
}

input, textarea, select, button {
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  color: #777;
}

textarea {
  min-height: 150px;
}

select {
  min-width: 50px;
  height: 32px; /*safari padding fix*/
  padding: 6px;
}

input[type=text]:focus,
input[type=email]:focus,
input[type=tel]:focus,
input[type=password]:focus,
textarea:focus,
select:focus {
  border-color: #ccc;
  color: #444;
  position: relative;
  z-index: 1;
}

input[type=checkbox],
input[type=radio],
input[type=checkbox] + *,
input[type=radio] + * {
  vertical-align: middle;
}

input[type=button], input[type=submit],
button, .button-1, .button-2 {
  cursor: pointer;
}

label, label + * {
  vertical-align: middle;
}

.master-wrapper-content {
  position: relative;
  z-index: 0;
  width: 90%;
  margin: 0 auto;
}

.master-column-wrapper {
  position: relative;
  z-index: 0;
}

.master-column-wrapper:after {
  content: "";
  display: block;
  clear: both;
}

.center-1 {
  margin: 0 0 100px;
}

.center-2, .side-2 {
  margin: 0 0 50px;
}

.side-2:after {
  content: "";
  display: block;
  clear: both;
}

.page {
  min-height: 200px;
  text-align: center;
}

.page-title {
  min-height: 50px;
  margin: 0 0 25px;
  border-bottom: 1px solid #ddd;
  padding: 6px 10px;
}

.page-title h1 {
  font-size: 30px;
  font-weight: normal;
}

.page:after,
.page-title:after,
.page-body:after {
  content: "";
  display: block;
  clear: both;
}

.center-1 .page-title,
.center-1 .title {
  text-align: center;
}

.buttons {
  margin: 0 0 30px;
}

.link-rss {
  display: none;
  width: 24px;
  height: 24px;
  background: url("../images/rss.png") center no-repeat;
  font-size: 0 !important;
}

.category-description ul,
.manufacturer-description ul,
.full-description ul,
.topic-block ul,
.topic-page ul,
.post-body ul {
  margin: 12px 0;
  padding: 0 0 0 36px;
  list-style: disc;
}

.category-description ol,
.manufacturer-description ol,
.full-description ol,
.topic-block ol,
.topic-page ol,
.post-body ol {
  margin: 12px 0;
  padding: 0 0 0 36px;
  list-style: decimal;
}

.category-description p,
.manufacturer-description p,
.full-description p,
.topic-block p,
.topic-page p,
.post-body p,
.news-body p {
  margin: 10px 0;
  text-align: justify;
}

.flyout-cart {
  display: none;
}

/*********** GLOBAL FORMS ***********/
.fieldset, .section {
  position: relative;
  margin: 0 0 40px;
}

.fieldset .title,
.section .title {
  margin: 0 0 15px;
  padding: 0 10px;
  font-size: 20px;
  color: #444;
}

.fieldset .title strong,
.section .title strong {
  font-weight: normal;
}

.form-fields {
  position: relative;
  border-top: 1px solid #e6e6e6;
  background-color: #f9f9f9;
  padding: 30px 15px;
}

.inputs {
  position: relative;
  margin: 0 0 15px;
  text-align: center;
  white-space: nowrap; /*fix for 'required' elements*/
  font-size: 0;
}

.inputs:after {
  content: "";
  display: block;
  clear: both;
}

.inputs label {
  display: block;
  width: 100%;
  margin: 0 0 10px;
  text-align: center;
  font-size: 14px; /*reset zeroing*/
  color: #444;
}

.inputs input[type=text],
.inputs input[type=email],
.inputs input[type=tel],
.inputs input[type=password],
.inputs select, .inputs textarea {
  width: 400px;
  max-width: 100%;
  background-color: #fff;
}

.inputs.custom-attributes {
  white-space: normal;
}

.inputs .option-list {
  display: inline-block;
  max-width: 95%;
  vertical-align: middle;
}

.inputs .option-list li {
  display: inline-block;
  margin: 5px;
  background-color: #fff;
}

.inputs .option-list label {
  display: inline-block;
  width: auto !important;
  margin: 0 0 0 5px;
}

.inputs.reversed {
  margin: 0 0 20px;
  text-align: center;
}

.required {
  margin: 0 -8px 0 3px; /*siblings offset*/
  vertical-align: top;
  font-size: 20px;
  font-weight: normal;
  color: #444;
}

.message-error,
.field-validation-error,
.username-not-available-status,
.poll-vote-error, .password-error {
  display: block;
  text-align: center;
  font-size: 13px;
  color: #e4434b;
  margin-top: 5px;
}

.field-validation-valid,
.username-available-status {
  display: block;
  text-align: center;
  font-size: 13px;
  color: #4cb17c;
}

.captcha-box {
  text-align: center;
  line-height: 0; /*firefox line-height bug fix*/
}

.captcha-box > div {
  display: inline-block;
  max-width: 100%;
}

.captcha-box input {
  height: auto;
}

/*********** GLOBAL TABLES ***********/
.table-wrapper {
  overflow-x: auto;
}

.data-table .hidden-row {
  height: 1px;
  border: none;
}

.data-table tr.hidden-row th {
  padding: 0;
  border-top: none;
}

.data-table tr.hidden-row th > span {
  height: 1px;
  width: 1px;
  position: absolute;
  overflow: hidden;
  top: -10px;
}

.cart th,
.data-table th,
.forum-table th {
  border-width: 1px 1px 0;
  border-style: solid;
  border-color: #ddd;
  background-color: #f6f6f6;
  padding: 10px;
  white-space: nowrap;
  font-weight: normal;
}

.forum-table th.replies,
.forum-table th.views,
.forum-table th.votes,
.forum-table th.latest-post,
.forum-table th.topics,
.forum-table th.posts,
.forum-table td.replies,
.forum-table td.views,
.forum-table td.votes,
.forum-table td.latest-post,
.forum-table td.topics,
.forum-table td.posts {
  display: none;
}

.forum-table td.image {
  min-width: 0;
}

.cart td,
.data-table td,
.forum-table td {
  min-width: 50px;
  border-width: 1px;
  border-style: solid;
  border-color: transparent #ddd #ddd;
  background-color: #fff;
  padding: 20px;
  color: #444;
}

.forum-table td {
  min-width: 90px;
  padding: 10px;
}

.cart a,
.data-table a,
.forum-table a {
  font-weight: normal;
  color: #4ab2f1;
}

.cart a:hover,
.cart a:focus,
.data-table a:hover,
.data-table a:focus,
.forum-table a:hover,
.forum-table a:focus {
  text-decoration: underline;
}

.cart .product,
.data-table .product,
.data-table .message,
.data-table .info,
.data-table .name {
  min-width: 225px;
  text-align: left;
}

.forum-table .forum-details,
.forum-table .topic-details {
  text-align: left;
}

.cart .product a,
.data-table .product a,
.data-table .info a {
  font-weight: bold;
  color: #444;
}

.cart .product a:hover,
.cart .product a:focus,
.data-table .product a:hover,
.data-table .product a:focus,
.data-table .info a:hover,
.data-table .info a:focus {
  color: #4ab2f1;
  text-decoration: none;
}

.cart .product .edit-item {
  margin: 10px 0 0;
}

.cart .product .edit-item a {
  font-weight: normal;
  color: #4ab2f1;
}

.cart .product .edit-item a:hover,
.cart .product .edit-item a:focus {
  text-decoration: underline;
}

.cart .remove-from-cart,
.cart .add-to-cart,
.data-table .select-boxes,
.data-table .order {
  text-align: center;
}

.cart .remove-from-cart input[type=checkbox] {
  display: none;
}

.cart .remove-from-cart .remove-btn {
  width: 11px;
  height: 11px;
  border: 0;
  background: #ffffff url(../images/remove.png) center no-repeat;
}

.cart td.unit-price,
.data-table td.unit-price {
  white-space: nowrap;
}

.cart td.quantity input {
  width: 50px;
  text-align: center;
}

.cart td.subtotal,
.data-table td.total {
  white-space: nowrap;
  color: #444;
}

.cart td.subtotal .discount, .cart td.subtotal .discount-additional-info {
  font-style: italic;
  color: #4ab2f1;
}

.data-table em a {
  font-weight: bold;
  color: #444;
}

.data-table div.download {
  margin: 10px 0 0;
}

/*********** NOTIFICATIONS & POPUPS  ***********/
.bar-notification-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  line-height: 16px;
  color: #fff;
  opacity: 0.95;
}

.bar-notification.success {
  background-color: #4bb07a;
}

.bar-notification.error {
  background-color: #e4444c;
}

.bar-notification.warning {
  background-color: #f39c12;
}

.bar-notification {
  position: relative;
  display: none;
  padding: 15px 25px 15px 10px;
}

.bar-notification .content {
  margin: 0 10px 0 0;
}

.bar-notification .content a {
  color: #fff;
  text-decoration: underline;
}

.bar-notification .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 32px;
  height: 32px;
  margin: 7px;
  background: #fff url("../images/close.png") center no-repeat;
  cursor: pointer;
}

.popup-notification {
  border-radius: 5px;
  padding: 3px;
  margin: 5px 0;
}

.popup-notification.success {
  border: 2px rgba(75, 176, 122, 0.5) solid;
}

.popup-notification.error {
  border: 2px rgba(228, 68, 76, 0.5) solid;
}

.popup-notification.warning {
  border: 2px rgba(243, 156, 18, 0.5) solid;
}

.noscript {
  border-bottom: 1px solid #333;
  background-color: #ff9;
  padding: 30px 15px;
  text-align: center;
  line-height: 22px;
  color: #444;
}

.ajax-loading-block-window {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 999;
  width: 32px;
  height: 32px;
  margin: -16px 0 0 -16px;
  background: url("../images/loading.gif") center no-repeat;
}

