.tooltip-container {
  position: absolute;
  width: 200px;
  color: #000000;
  background: #FFFFFF;
  border: 1px solid #eeeeee;
  text-align: center;
  visibility: hidden;
  border-radius: 5px;
}

.tooltip-container:before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -7px;
  width: 0;
  height: 0;
  border-bottom: 7px solid #eeeeee;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
}

.tooltip-container:after {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -6px;
  width: 0;
  height: 0;
  border-bottom: 6px solid #FFFFFF;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}

