.html-wishlist-page #profile-tab {
  background: url("../images/Icon_Items_inactive.png") no-repeat;
  color: unset;
  border: unset;
  border-color: unset;
  font-size: 0;
  height: 95px;
  width: 95px;
  background-size: 70%;
  background-position: bottom;
}

.store-content .store-list li.vendor-name {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}

.store-content .store-list .vendor-delivery, .store-content .store-list .vendor-distanceTime {
  display: flex;
}

.store-content .store-list .vendor-distanceTime {
  margin-top: 10px;
}

.store-content .store-list .vendor-delivery {
  justify-content: flex-start;
  font-size: 18px;
  gap: 10px;
  margin-top: 5px;
}

.store-content .store-list .vendor-mobile {
  margin-top: 8px;
}

.store-content .store-list .vendor-picture img {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
}

.store-content .store-list .vendor-deliver {
  margin-right: 10px;
}

.html-account-page.html-wishlist-page .side-2 {
  padding-top: 11%;
}

.html-account-page.html-wishlist-page .vendor-name strong {
  font-size: 20px;
  font-weight: 600;
  color: #24282F;
}

.html-wishlist-page .nav-tabs .Items-1 {
  margin-right: 80px;
  text-align: center;
}

.html-wishlist-page .nav-tabs .items-button {
  text-align: center;
}

.html-wishlist-page .nav-tabs .Items-1 span, .nav-tabs .items-button span {
  font-weight: bold;
  font-size: 20px;
  color: #000;
}

.html-wishlist-page .nav-tabs {
  margin-bottom: 33px;
  border: 0;
}

.html-account-page.html-wishlist-page .master-wrapper-content {
  padding: 45px 5vw;
}

.html-account-page.html-wishlist-page .side-2 .block-account-navigation .listbox ul li a {
  font-size: 14px;
  font-weight: 600;
  color: #b7b7b7;
}

.html-account-page.html-wishlist-page .side-2 .block-account-navigation .listbox ul li a:after {
  font-size: 25px;
}

#favorite-toggle .search-favorites-store-lbl {
  font-size: 18px;
  color: #58595B;
  font-weight: 500;
}

.html-account-page.html-wishlist-page .side-2 {
  width: 270px;
}

.html-account-page.html-wishlist-page .center-2 {
  width: calc(100% - 270px);
}

.html-account-page.html-wishlist-page .store-content .store-list {
  height: 100%;
  max-height: 100%;
  margin-bottom: unset;
}

.vendor-picture .Img-box {
  position: relative;
  display: block;
  padding-bottom: 68%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: hidden;
}

.vendor-address a.vendor-address-location, .vendor-mobile a.vendor-mobile {
  font-size: 18px;
  color: #484D55;
}

.vendor-mobile a.vendor-mobile {
  font-weight: 600;
}

.vendor-time {
  font-size: 16px;
  text-align: right;
}

.html-account-page.html-wishlist-page .items-sections .edit-item a {
  color: #f0862e;
}

.html-wishlist-page .favorite-link-wrapper th {
  padding: 20px;
  white-space: nowrap;
  font-size: 14px;
  color: #0f121b;
  font-weight: 300;
}

.k-web .vendor-distance {
  display: flex;
  margin-right: auto;
}

.k-web span.vendor-distance {
  white-space: nowrap;
}

.k-web span.vendor-distance strong {
  margin-left: 5px;
  font-size: 16px;
}

@media (max-width: 580px) {
  .html-wishlist-page #home-tab, .html-wishlist-page #profile-tab {
    height: 64px;
    width: 60px;
    background-size: 75%;
  }
  #favorite-toggle .modal-dialog.delivery-section .modal-content .modal-header {
    padding: 15px 0px;
  }
  #favorite-toggle .favorite-instruction-popup-popup {
    font-size: 14px;
    text-align: left;
  }
  #favorite-toggle .modal-footer {
    display: block;
  }
  .html-account-page.html-wishlist-page .center-2 {
    width: unset;
  }
  .html-account-page.html-wishlist-page .wishlist-page .page-body {
    padding: 1px 10px 30px;
  }
  .html-account-page .store-list .right-data .vendor-delivery {
    display: flex;
    justify-content: end;
  }
  .html-account-page.html-wishlist-page .vendor-is-pickup, .html-account-page.html-wishlist-page .vendor-deliver {
    white-space: nowrap;
  }
  #home-tab.active, #profile-tab, #home-tab, #profile-tab.active {
    height: 64px;
    width: 60px;
    background-size: 60px;
  }
  .html-wishlist-page .nav-tabs .Items-1 {
    margin-right: 30px;
  }
  .html-wishlist-page .nav-tabs {
    justify-content: center;
  }
  .html-wishlist-page .nav-tabs .Items-1 span, .nav-tabs .items-button span {
    font-size: 14px;
  }
  .html-account-page.html-wishlist-page .master-wrapper-content {
    padding: unset;
  }
  .html-account-page.html-wishlist-page .side-2 {
    width: unset;
  }
  .html-account-page.html-wishlist-page .master-wrapper-content {
    margin-top: unset;
  }
  .html-wishlist-page .nav-tabs {
    margin-bottom: 12px;
  }
  .html-account-page.html-wishlist-page .wishlist-page .page-body {
    background: #fff;
  }
  .wishlist-page .Toggle-Fav {
    margin: unset;
    justify-content: space-between;
    align-items: center;
    background: #F2F1F1;
    border-radius: 5px;
    padding: 6px 18px;
  }
  #favorite-toggle .search-favorites-store-lbl {
    font-size: 13px;
  }
  .html-account-page.html-wishlist-page .switch {
    width: 35px;
    height: 20px;
  }
  .html-account-page.html-wishlist-page .slider:before {
    height: 15px;
    width: 15px;
    left: 1px;
    bottom: 0px;
  }
  .html-account-page.html-wishlist-page input:checked + .slider:before {
    -webkit-transform: translateX(12px);
    -ms-transform: translateX(12px);
    transform: translateX(12px);
  }
  #favorite-toggle button.btn.btn-default.font-weight-bold img {
    border: unset;
    background: unset;
    height: 13px;
    width: 13px;
    vertical-align: top;
  }
  #favorite-toggle button.btn.btn-default.font-weight-bold {
    background: transparent;
    padding: 3px 1px;
  }
  .html-account-page.html-wishlist-page .store-content .store-list {
    margin-top: unset;
    border: unset;
    border-radius: unset;
    padding: unset;
    background: unset;
    border-bottom: 1px solid #e4e4e4;
    padding-bottom: 8px;
    padding-top: 8px;
  }
  .html-account-page.html-wishlist-page .vendor-picture-mobile .Img-box {
    border-top-left-radius: unset;
    border-top-right-radius: unset;
    overflow: hidden;
    border-radius: 5px;
    object-fit: cover;
    display: inline-block;
  }
  .html-account-page.html-wishlist-page .store-content .store-list {
    margin-bottom: unset;
  }
  .html-account-page.html-wishlist-page .vendor-name strong {
    font-size: 14px;
  }
  .html-account-page.html-wishlist-page .vendor-address a.vendor-address-location, .vendor-mobile a.vendor-mobile, .store-content .store-list .vendor-delivery {
    font-size: 13px;
  }
  .html-account-page.html-wishlist-page .vendor-is-favorite .active {
    height: 16px;
    width: 16px;
    background-size: 16px;
  }
  .html-account-page.html-wishlist-page .store-content .store-list .vendor-mobile, .store-content .store-list .vendor-distanceTime {
    margin-top: unset;
  }
  .html-account-page.html-wishlist-page .store-content .store-list .vendor-delivery {
    margin-bottom: 15px;
  }
  .html-account-page.html-wishlist-page .vendor-time {
    font-size: 13px;
  }
  .html-wishlist-page .wishlist-page .page-body {
    padding: 0;
  }
  .html-wishlist-page .vendor-address {
    float: left;
  }
  .vendor-distance {
    font-size: 13px;
    margin-bottom: 5px;
  }
  .mobile-data .vendor-distance img {
    height: 16px;
    width: 13px;
    margin-right: 2px;
  }
  .mobile-data .vendor-delivery .vendor-is-pickup img, .mobile-data .vendor-delivery .vendor-deliver img {
    height: 16px;
    width: 16px;
    margin-right: 0px;
  }
  .store-content .store-list .vendor-deliver {
    margin-right: 0px;
  }
  .store-content .store-list .vendor-delivery, .store-content .store-list .vendor-distanceTime {
    display: block;
  }
  .store-list .mobile-data .right-data {
    float: right;
    text-align: right;
    max-width: 50%;
  }
  .store-list .left-data {
    float: left;
    text-align: left;
    max-width: 50%;
    margin-bottom: 12px;
  }
  .vendor-picture-mobile .Img-box {
    width: 100%;
    display: block;
  }
  .vendor-picture-mobile .Img-box img {
    width: 100%;
  }
  .left-data .vendor-address .vendor-address-location {
    line-height: 23px;
  }
}
@media (max-width: 320px) {
  .wishlist-page .Toggle-Fav {
    padding: 6px 12px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .store-content .store-list .vendor-distanceTime {
    justify-content: space-between;
  }
  .html-account-page.html-wishlist-page .center-2, .html-account-page.html-wishlist-page .side-2 {
    width: 100%;
  }
  .html-account-page.html-wishlist-page .master-wrapper-content {
    padding: 0;
  }
  .html-account-page.html-wishlist-page .side-2 {
    padding-top: 0;
  }
  .html-wishlist-page .nav-tabs {
    margin-left: 40px;
  }
}
@media (min-width: 1200px) and (max-width: 1440px) {
  .html-account-page.html-wishlist-page .vendor-name strong {
    font-size: 16px;
  }
  .vendor-address a.vendor-address-location, .vendor-mobile a.vendor-mobile {
    font-size: 16px;
  }
  .store-content .store-list .vendor-delivery {
    font-size: 16px;
    margin-top: 5px;
  }
  .html-account-page.html-wishlist-page .side-2 {
    padding-top: 14%;
  }
}
@media (min-width: 1201px) and (max-width: 1600px) {
  .store-content .col-3-laptop {
    width: 33.3%;
  }
}