}

.inputs .option-list {
display: inline-block;
max-width: 95%;
vertical-align: middle;
}

.inputs .option-list li {
display: inline-block;
margin: 5px;
background-color: #fff;
}

.inputs .option-list label {
display: inline-block;
width: auto !important;
margin: 0 0 0 5px;
}

.inputs.reversed {
